<template>
	<section>
		<!---->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters">
				<el-form-item>
					<el-input v-model="filters.content" clearable placeholder=""></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="getMessageList"></el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="handleAdd"></el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<!---->
		<el-table :data="messages" highlight-current-row v-loading="listLoading" style="width: 100%;" :border="true">
			<el-table-column type="index" width="60" align="center">
			</el-table-column>
			<el-table-column prop="author" label="" width="120" align="center">
			</el-table-column>
			<el-table-column prop="content" label="" width="350" align="center">
			</el-table-column>
			<el-table-column prop="isshow" label="" width="100" align="center" :formatter="formatIsshow">
			</el-table-column>
			<el-table-column prop="savetime" label="" width="160" sortable align="center">
			</el-table-column>
			<el-table-column prop="changetime" label="" width="160" sortable align="center">
			</el-table-column>
			<el-table-column label="" width="150" align="center" fixed="right">
				<template scope="scope" >
					<el-button size="small" @click="handleEdit(scope.$index, scope.row)"></el-button>
					<el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)"></el-button>
				</template>
			</el-table-column>
		</el-table>

		<!---->
		<el-dialog title="" :visible.sync="editFormVisible" :close-on-click-modal="false">
			<el-form :model="editForm" label-width="120px" :rules="editFormRules" ref="editForm">
		
				<el-form-item label="" prop="author">
					<el-input v-model="editForm.author"></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-input type="textarea"  :rows="8" v-model="editForm.content"></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-radio-group v-model="editForm.isshow">
						<el-radio class="radio" :label="true"></el-radio>
						<el-radio class="radio" :label="false"></el-radio>
					</el-radio-group>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="editFormVisible = false"></el-button>
				<el-button type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
			</div>
		</el-dialog>
	</section>
</template>

<script>
	import util from '../../common/js/util'
	//import NProgress from 'nprogress'
	import { getMessageAll, addMessage, editMessage, removeMessage} from '../../api/api';

	export default {
		data() {
			return {
				filters: {
                    content: '',
                    messagetypenumber: ''
				},
				messages: [],
				listLoading: false,
				editFormVisible: false,//
				editLoading: false,
				editFormRules: {
					rounterchart: [
						{ required: true, message: '', trigger: 'blur' }
					]
				},
				//
				editForm: {
					messageid: null,
					author: '',
					content: '',
					isshow: true,
					messagetypenumber: ''
				},

			}
		},
		methods: {
			formatIsshow: function (row, column) {
				return row.isshow == 1 ? '' : row.isshow == 0 ? '' : '';
			},
			getMessageList() {
				let para = {
                    content: this.filters.content,
                    messagetypenumber: this.filters.messagetypenumber,
				};
				this.listLoading = true;
				getMessageAll(para).then((res) => {
                    console.log(res.data)
					this.messages = res.data;
					this.listLoading = false;

				});
			},
			//
			handleDel: function (index, row) {
				this.$confirm('?', '', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = { ids: row.messageid };
					removeMessage(para).then((res) => {
                            this.listLoading = false;
                            this.$message({
                                message: '',
                                type: 'success'
                            });
                            this.getMessageList();
                        });
                    }).catch(() => {
				});
			},
			//
			handleEdit: function (index, row) {		
				this.editForm = Object.assign({}, row);
				this.editFormVisible = true;
			},
			//
			handleAdd: function () {
				this.editFormVisible = true;
				this.editForm = {
                    messageid: null,
					author: '',
					content: '',
					isshow: true,
					messagetypenumber: ''
				};
			},
			//
			editSubmit: function () {
				this.$refs.editForm.validate((valid) => {
					if (valid) {
						this.$confirm('', '', {}).then(() => {
							this.editLoading = true;
							let para = Object.assign({}, this.editForm);
							if(para.messageid!=null){//ID
								editMessage(para).then((res) => {
									this.editLoading = false;
									this.$message({
										message: '',
										type: 'success'
									});
									this.$refs['editForm'].resetFields();
									this.editFormVisible = false;
									this.getMessageList();
								});
							}else{
								addMessage(para).then((res) => {
									this.editLoading = false;
									this.$message({
										message: '',
										type: 'success'
									});
									this.$refs['editForm'].resetFields();
									this.editFormVisible = false;
									this.getMessageList();
								});	
							}	
						});
					}
				});
			},
		},
		mounted() {
			this.getMessageList();
		}
	}
</script>

<style scoped>
</style>


